<template>
    <div>
        <van-nav-bar
            title="修改密码"
            left-text="返回"
            left-arrow
            @click-left="clickReturn"
        />
        <van-field
            type="text"
            label="旧密码"
            placeholder="旧密码"
            v-model="oldPassword"
        />
        <van-field
            type="text"
            label="新密码"
            placeholder="新密码"
            v-model="password"
        />
        <div class="button-box">
            <van-button type="info" @click="change">确认修改</van-button>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                password:'',
                oldPassword:''
            }
        },

        methods:{
            change(){
                this.axios({
                    method: 'POST',
                    url: '/updatePassword',
                    data: {
                        appkey: this.appkey,
                        tokenString: localStorage.getItem("__Tk"),
                        password: this.password,
                        oldPassword: this.oldPassword
                    }
                }).then(result => {
                    this.$toast.clear();
                    this.$toast({
                        message: result.data.msg
                    });
                    if(result.data.code=='E001'){
                        this.$router.push('/login');
                    }
                }).catch(err => {
                    this.$toast.clear();
                })
            },

            clickReturn(){
                this.$router.go(-1);
            }
        }
    }
</script>

<style lang="less" scoped>
.button-box{
    width: 88px;
    overflow: hidden;
    margin: 20px auto;
}
</style>